 p{
  font-size: 13px;
  margin: 0px;
  padding: 0px;
}

nav{
  padding: 30px 40px;
  box-shadow: 0px 2.0px 7.4px rgba(0, 0, 0, 0.1);
}

.hero{
  display: flex;
  flex-direction: column;
  padding: 50px;
}

.airbnb--logo{
  height: 40px;
}

.hero--pics{
  width: auto;
}

.hero-text{
  margin: none;
  width: 400px;
}

.card-list{
  display: flex;
  flex-wrap: nowrap;
  gap: 20px;
  overflow-x: auto;
  justify-content: center;
}

.card{
  position: relative;
  width: 175px;
  font-size: 12px;
  /* flex: 0 0 auto;          */
  display: flex;          
  flex-direction: column; 
}

.card--badge{
  position: absolute;
  top: 6px;
  left: 7px;
  background-color: white;
  padding: 5px 7px;
  border-radius: 5px;
  font-weight: bold;
}

.airbnb-section{
  width: 100%;
  border-radius: 9px;
  margin-bottom: 9px;
}

.airbnb-image{
  width: 100%;
  border-radius: 9px;
  margin-bottom: 9px;
  /* max-width: 175px; */
  /* margin: none; */
}

.airbnb-rating-flex{
  display: flex;
  align-items: center;
  margin: none;
}

.star-img{
  height: 15px;
  padding-right: 5px;
}

.gray{
  color: gray;
}

.bold{
  font-weight: bold;
} 

.render{
  display: flex;
}
